import React, { PureComponent, Suspense } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import { isTagShop } from "../../../../actions/api";
import Image from "../../../Themes/Elements/Media/Image";
const ReelVideo = React.lazy(() => import("./ReelVideo"));

const mediaDimension = JSON.parse(
  window.localStorage.getItem("mediaDimension")
);
export default class ReelMediaSlider extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      mediaSize: {
        height: mediaDimension != null ? mediaDimension[0] : "85vh",
        width: mediaDimension != null ? mediaDimension[1] : "85vh",
      }
    }
  }
  mediaSize = (mediaSize) => this.props.mediaSizeCallback(mediaSize);
  render() {
    const { Post, wallID, size, onUpdateProdImgIdx } = this.props;
    const muted = false;
    const pagination = {
      clickable: true,
      bulletClass: `tb_rp_bullet`,
    };

    return (
      <div className="tb_rp_media_holder">
        <Swiper
          ref={this.splideRef}
          direction="horizontal"
          passiveListeners={true}
          pagination={pagination}
          onSlideChange={(swiper) => onUpdateProdImgIdx(swiper.activeIndex)}
        >
          {Post.mediaList.map((imageItem, index) => (
            <SwiperSlide key={index}>
              {({ isActive }) => (
                <>
                  {[2, 4].includes(imageItem.type) || !isActive ? (
                    <Image
                      ImageClass="tb_rp_s_image"
                      Post={Post}
                      mediaOnly={imageItem}
                      size={size}
                      hotspot={imageItem.product.length ? isTagShop : false}
                    />
                  ) : [3, 5].includes(imageItem.type) ? (
                      <ReelVideo
                        VideoClass="tb_rp_video"
                        media={imageItem.video.full}
                        mediaPoster={imageItem.image.original}
                        Post={{...Post, media : { video : imageItem.video , image : imageItem.image }}}
                        wallID={wallID}
                        size={size}
                        isCover={true}
                        controls={false}
                        autoPlay={isActive}
                        muted={muted}
                        index={index}
                      />
                  ) : null}
                </>
              )}
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    );
  }
}